import { Component } from '@angular/core';
import { IonicPage, ModalController, NavController, ViewController } from 'ionic-angular';
import { DrugsProvider } from '../../providers/drugs/drugs';
import { DrugModel } from '../../models/drug.model';

/**
 * Generated class for the DrugsPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage({
  name: 'DrugsPage'
})
@Component({
  selector: 'page-drugs',
  templateUrl: 'drugs.html',
})
export class DrugsPage {

  drugName: string;

  currentItems: DrugModel[] = [];

  page: number = 0;

  q: string = '';

  constructor(public navCtrl: NavController, private viewCtrl: ViewController,
              public drugs: DrugsProvider, public modalCtrl: ModalController) {}

  ionViewDidLoad() {
    console.log('ionViewDidLoad DrugsPage');
    this.getItems();
  }

  getItems(params?: any) {
    return new Promise((resolve) => {
      this.drugs.query(params).subscribe((data: DrugModel[]) => {
        if (params && params.q) {
          this.currentItems = data;
          resolve();
          return;
        }
        this.currentItems = this.currentItems.concat(data);
        resolve();
      });
    });
  }

  doInfinite(infiniteScroll) {
    return new Promise((resolve) => {
      console.log('doInfinite');
      this.page++;

      this.getItems({page: this.page}).then(() => {
        resolve();
        infiniteScroll.complete();
      });
    });
  }

  onCancel() {
    this.page = 0;
    this.q = '';
    this.getItems();
  }

  getDrugs(ev: any) {
    const val = ev.target.value;

    if (val === '') {
      this.q = '';
      this.page = 0;
      this.getItems({page: this.page, q: this.q});
      return;
    }

    // if the value is an empty string don't filter the items
    if (val && val.trim() != '' && val.length > 3) {
      this.q = val;
      this.page = 0;
      this.getItems({page: this.page, q: this.q});
    }
  }

  /**
   * Prompt the user to add a new item. This shows our ItemCreatePage in a
   * modal and then adds the new item to our data source if the user created one.
   */
  detailDrugDescription(drug) {
    let detailDrugDescriptionModal = this.modalCtrl.create('DrugDetailPage', {drug: drug});

    detailDrugDescriptionModal.present();
  }

  dismiss() {
    this.viewCtrl.dismiss();
  }


}



